* {
  background: #B4C5E4;
  color: #3C3744;
}

#App {
  display:flex;
  justify-content: center;
  align-items: center;
  height:90vh;
  flex-direction: column;
}

#App #textarea {
  background: #3C3744;
  color: #B4C5E4;
  font-size: 20px;
  transition: 0.2s;
}

#App #textarea:hover {
  background: #B4C5E4;
  color:#3C3744;
}

#App #submit-btn {
  width: 15%;
  border-radius: 10px;
  transition: 0.4s;
}

#App #submit-btn:hover {
  background-color:#3C3744;
  color:#B4C5E4;
}

span {
  font-size: 16px;
}

@media (max-width: 768px) {
  #App {
    text-align: center;
  }

  #App #textarea {
    max-width: 80vw; 
  }
    
  #App #submit-btn {
      min-width: 20%;
  }
}